.svg-table-cell-container {
  cursor: url("data:image/png;base64,"),
    auto;
  rect {
    fill: var(--gray3);
    stroke: var(--gray5);
    stroke-width: 1;
  }
  text {
    font-size: 10pt;
    fill: var(--gray15);
  }
  &:hover {
    rect {
      fill: var(--gray5);
    }
  }
}

.being-selected {
  rect {
    fill: var(--highlight-blue);
  }
}

.being-dragging {
  cursor: url("data:image/png;base64,"),
    auto;
  rect {
    fill: var(--gray7);
  }
}
