.attr-sort {
  display: block;
  width: 100%;
  height: 24px;
  line-height: 24px;
  text-align: center;
  border-radius: 2px;
  background: var(--gray3);
  color: var(--gray13);
  margin: 6px 0;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  transition: all 0.2s cubic-bezier(0.445, 0.05, 0.55, 0.95);
  select {
    float: left;
    box-sizing: border-box;
    padding: 0 6px;
    border: none;
    outline: none;
    width: 100%;
    height: 24px;
    line-height: 24px;
    background: none;
    appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    color: var(--gray13);
    option {
      color: var(--gray13);
      width: calc(100% + 14px);
    }
    &:hover {
      cursor: pointer;
    }
  }
  &:after {
    content: "";
    width: 14px;
    height: 14px;
    margin: 5px 0 0 -14px;
    background-image: url(data:image/svg+xml;base64,);
    background-repeat: no-repeat;
    background-size: 100% auto;
    position: absolute;
    pointer-events: none;
  }
  &:hover {
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.3);
    cursor: pointer;
  }
}
