@import "./scroller.scss";
@import url("https://fonts.googleapis.com/css?family=Roboto:100,300,400&display=swap");
@font-face {
  font-family: "geforce";
  src: url("../font/geforce.woff2") format("woff2"),
    url("../font/geforce.woff") format("woff"),
    url("../font/geforce.ttf") format("truetype"),
    url("../font/geforce.otf") format("opentype");
}

@font-face {
  font-family: "mono";
  src: url("../font/UbuntuMono-Regular.ttf") format("truetype");
}

:root {
  --nav-content-height: 20px;
  --widget-font-size: 11pt;
  --title-height: 26px;
  --geforce-font: "geforce";
  --mono-font: "mono";
  --gray1: #ffffff;
  --gray2: #f7f7f7;
  --gray3: #f2f2f2;
  --gray4: #ededed;
  --gray5: #e6e6e6;
  --gray6: #e2e2e2;
  --gray7: #dadada;
  --gray8: #d2d2d2;
  --gray9: #c2c2c2;
  --gray10: #a5a5a5;
  --gray11: #a3a3a3;
  --gray12: #939393;
  --gray13: #676767;
  --gray14: #484848;
  --gray15: #383838;
  --gray16: #000000;
  --highlight-blue: #2196f3;
  --highlight-orange: #ff9246;
  --selected-bg: #cbeaff;
  --layer-level-m1: -10;
  --layer-level-0: 10;
  --layer-level-1: 20;
  --layer-level-2: 30;
  --layer-level-3: 40;
  --layer-level-4: 50;
  --layer-level-5: 60;
  --layer-level-6: 70;
}

.zero-margin-padding {
  margin: 0;
  padding: 0;
}

.cover-size {
  width: 100%;
  height: 100%;
}

* {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
}

html,
body {
  @extend .zero-margin-padding;
  @extend .cover-size;
  background: var(--gray3);
  font-family: "Lato", "Lucida Grande", "Segoe UI", sans-serif;
  overflow: hidden;
  touch-action: none;
}

.outer-wrapper {
  @extend .zero-margin-padding;
  @extend .cover-size;
  display: flex;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
}

.inner-wrapper {
  flex: 1;
  height: 500px !important;
}

.footer {
  width: 100%;
  height: 22px;
  color: var(--gray1);
  background: var(--highlight-blue);
}

.ease-transform {
  transition: transform 0.1s cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.ease-fade {
  transition: opacity 0.1s cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.ease-fill {
  transition: fill 0.1s cubic-bezier(0.445, 0.05, 0.55, 0.95);
}

.drop-shadow-ele {
  filter: url(#hoverShadow);
}

.draggable-component {
  cursor: move;
}

.grabbing-ele {
  cursor: url("data:image/png;base64,"),
    auto;
}

.clickable-component {
  cursor: pointer;
}

.stretchable-component {
  cursor: ew-resize;
}

.fadein-ele {
  opacity: 0;
}

.fadein-ele:hover {
  opacity: 1;
}

.show-ele {
  opacity: 1;
}

.hide-ele {
  opacity: 0;
}

.no-display-ele {
  display: none;
}

.ele-under {
  z-index: var(--layer-level-m1);
}

.wait {
  cursor: wait;
}

.monospace-font {
  font-family: var(--mono-font);
}

.small-font {
  font-size: 9pt;
}

.preview-btn-container {
  position: absolute;
  // background: rgba(255, 255, 255, 0.3);
  // box-shadow: 0 3px 5px rgba(0, 0, 0, 0.16);
  z-index: var(--layer-level-6);
}

.test-div,
.test-btn {
  position: absolute;
  left: 100px;
  top: 100px;
  width: 100px;
  height: 100px;
  background: goldenrod;
  z-index: 1000000;
  border-radius: 50%;
}

.test-btn {
  left: 300px;
}
