.data-view-content {
  background: none;
  display: flex;
  flex-direction: column;
}

.attr-wrapper {
  margin-top: 6px;
  box-sizing: border-box;
  padding: 0px 10px;
  width: 100%;
  display: flex;
  & div {
    clear: both;
    float: left;
    &:first-child {
      flex: 3;
    }
    &:last-child {
      flex: 2;
      transition: all 0.2s cubic-bezier(0.445, 0.05, 0.55, 0.95);
    }
    div {
      width: 100%;
    }
  }
}

.non-sortable-attr-wrapper {
  & > div {
    &:last-child {
      flex: 0;
      width: 0;
      opacity: 0;
    }
  }
  #attrBtnContainer span {
    width: 100%;
  }
}

.data-column-title {
  float: left;
  text-transform: capitalize;
  font-size: 10pt;
  display: block;
  box-sizing: border-box;
  padding: 0 4px;
  width: 100%;
  height: 26px;
  line-height: 26px;
  background: var(--gray7);
  color: var(--gray14);
}

.data-table-wrapper {
  box-sizing: border-box;
  // padding: 0px 10px;
  // margin-top: 10px;
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: auto;
  .data-table-title {
    box-sizing: border-box;
    padding: 0 4px;
    height: 26px;
    line-height: 26px;
    background: var(--gray7);
    .data-table-title-text {
      float: left;
      text-transform: capitalize;
      color: var(--gray14);
      font-size: 10pt;
    }
    .drop-down {
      float: right;
      width: 14px;
      height: 14px;
      margin: 6px;
      background-color: none;
      background-image: url(data:image/svg+xml;base64,);
      background-repeat: no-repeat;
      background-size: 100% auto;
      transform: rotate(180deg);
      filter: invert(50%);
      &:hover {
        filter: invert(0%);
        cursor: pointer;
      }
    }
    .left-up-icon {
      transform: rotate(0deg);
    }
  }
  .data-table-container {
    width: 100%;
    flex: 1;
    overflow: auto;
    // margin-top: 6px;
    transition: all 0.2s cubic-bezier(0.445, 0.05, 0.55, 0.95);
  }
  .hidden-data-table {
    flex: none;
    height: 0px;
    overflow: hidden;
  }
}

